.checkout-component {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	list-style: none;

	.checkout-code {
		input {
			text-align: center;
			border-radius: 10px;
			border: 2px solid green;
			color: green;
			padding: 8px;
			margin: 50px 0;
			width: 150px;
		}

		.apply-btn {
			background: #78be78;
			width: 80px;
			color: #000;
			margin-left: 25px;

			@media not all and (hover: none) {
				&:hover {
					color: #fff;
				}
			}
		}
	}

	.checkout-shipping {
		display: flex;
		margin-top: 20px;

		input {
			width: 20px;
			height: 20px;
			margin-right: 10px;
		}

		label {
			line-height: 22px;
			font-size: 22px;
		}
	}

	.checkout-total,
	.discount {
		color: #f87428;
		font-weight: 900;
	}

	.checkout-btn {
		background: orange;
		border-radius: 8px;
		padding: 10px;
		border: none;
		box-shadow: inset 0px 0px 0px 2px #f87428;
		margin: 25px 0;

		@media not all and (hover: none) {
			&:hover {
				color: #fff;
			}
		}
	}
}

.hide {
	display: none;
}

@media (max-width: 767px) {
	.checkout-component {
    align-items: center;
    
		.checkout-code {
			text-align: center;
		}
	}
}